import axios from "axios";
import Cookies from "js-cookie";
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import {
  Pencil,
  Trash2,
  UserPlus,
  Search,
  X,
  ChevronDown,
  RotateCcw,
} from "lucide-react";
import { useMemo, useState } from "react";

interface AdminUser {
  _id: string;
  first_name: string;
  last_name: string;
  email: string;
  status: string;
  role: string;
}

const SkeletonRow = () => (
  <tr className="border-t border-zinc-200 dark:border-zinc-800 animate-pulse">
    <td className="p-4">
      <div className="h-4 bg-zinc-200 dark:bg-zinc-800 rounded w-3/4"></div>
    </td>
    <td className="p-4">
      <div className="h-4 bg-zinc-200 dark:bg-zinc-800 rounded w-full"></div>
    </td>
    <td className="p-4">
      <div className="h-4 bg-zinc-200 dark:bg-zinc-800 rounded w-12"></div>
    </td>
    <td className="p-4">
      <div className="h-4 bg-zinc-200 dark:bg-zinc-800 rounded w-16"></div>
    </td>
    <td className="p-4 text-right">
      <div className="h-8 w-16 bg-zinc-200 dark:bg-zinc-800 rounded ml-auto"></div>
    </td>
  </tr>
);

const AdminPanel = () => {
  const queryClient = useQueryClient();
  const [searchTerm, setSearchTerm] = useState("");
  const [filterStatus, setFilterStatus] = useState("");
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingAdmin, setEditingAdmin] = useState<AdminUser | null>(null);

  const [formData, setFormData] = useState({
    first_name: "",
    last_name: "",
    email: "",
    password: "",
    role: "admin",
    status: "faol",
  });

  const BASE_URL = process.env.NEXT_PUBLIC_BASE_URL;
  const token = Cookies.get("token");

  const { data: admins = [], isLoading } = useQuery({
    queryKey: ["admins", filterStatus],
    queryFn: async () => {
      if (!token) return [];
      const res = await axios.get(
        `${BASE_URL}/api/staff/all-admins?status=${filterStatus}`,
        { headers: { Authorization: `Bearer ${token}` } },
      );
      return res.data?.data || [];
    },
    enabled: !!token,
  });

  const deleteMutation = useMutation({
    mutationFn: (id: string) =>
      axios.delete(`${BASE_URL}/api/staff/deleted-admin`, {
        headers: { Authorization: `Bearer ${token}` },
        data: { _id: id },
      }),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["admins"] });
      alert("Muvaffaqiyatli o'chirildi");
    },
    onError: (err: any) => {
      alert(
        err.response?.status === 403
          ? "Huquqingiz yetarli emas!"
          : "Xatolik yuz berdi",
      );
    },
  });

  const saveMutation = useMutation({
    mutationFn: (payload: any) => {
      const endpoint = editingAdmin
        ? "/api/staff/edited-admin"
        : "/api/staff/create-admin";
      return axios.post(`${BASE_URL}${endpoint}`, payload, {
        headers: { Authorization: `Bearer ${token}` },
      });
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["admins"] });
      setIsModalOpen(false);
      alert("Muvaffaqiyatli saqlandi!");
    },
    onError: (err: any) => {
      alert(err.response?.data?.message || "Xatolik yuz berdi");
    },
  });

  const statusMutation = useMutation({
    mutationFn: (admin: AdminUser) => {
      const isRestoring = admin.status === "ishdan bo'shatilgan";
      const newStatus = isRestoring ? "faol" : "ishdan bo'shatilgan";
      return axios.post(
        `${BASE_URL}/api/staff/edited-admin`,
        { ...admin, status: newStatus },
        { headers: { Authorization: `Bearer ${token}` } },
      );
    },
    onSuccess: () => queryClient.invalidateQueries({ queryKey: ["admins"] }),
  });

  const filteredData = useMemo(() => {
    return admins.filter((admin: AdminUser) =>
      `${admin.first_name} ${admin.last_name} ${admin.email}`
        .toLowerCase()
        .includes(searchTerm.toLowerCase()),
    );
  }, [searchTerm, admins]);

  const handleStatusToggle = (admin: AdminUser) => {
    const isRestoring = admin.status === "ishdan bo'shatilgan";
    if (
      confirm(
        isRestoring ? "Ishga qaytarmoqchimisiz?" : "Ishdan bo'shatmoqchimisiz?",
      )
    ) {
      statusMutation.mutate(admin);
    }
  };

  const handleDelete = (id: string) => {
    if (confirm("Haqiqatan ham o'chirmoqchimisiz?")) {
      deleteMutation.mutate(id);
    }
  };

  const openModal = (admin: AdminUser | null = null) => {
    if (admin) {
      setEditingAdmin(admin);
      setFormData({ ...admin, password: "" });
    } else {
      setEditingAdmin(null);
      setFormData({
        first_name: "",
        last_name: "",
        email: "",
        password: "",
        role: "admin",
        status: "faol",
      });
    }
    setIsModalOpen(true);
  };

  const handleSaveSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const payload = editingAdmin
      ? {
          _id: editingAdmin._id,
          first_name: formData.first_name,
          last_name: formData.last_name,
          email: formData.email,
          status: formData.status,
        }
      : {
          ...formData,
          role: formData.role.toLowerCase(),
          work_date: new Date().toISOString().split("T")[0],
        };

    saveMutation.mutate(payload);
  };

  return (
    <div className="w-full p-2 sm:p-4 md:p-6 min-h-screen overflow-x-hidden">
      <div className="max-w-6xl mx-auto">
        {/* Header and Controls */}
        <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4 mb-6">
          <h1 className="text-lg md:text-xl font-semibold">
            Adminlar boshqaruvi
          </h1>
          <div className="flex flex-wrap items-center gap-2 w-full md:w-auto">
            <div className="relative flex-1 min-w-[140px]">
              <Search
                className="absolute left-3 top-1/2 -translate-y-1/2 text-zinc-500"
                size={14}
              />
              <input
                type="text"
                placeholder="Qidiruv..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full bg-background border border-zinc-800 rounded-lg py-1.5 pl-8 pr-2 text-xs md:text-sm outline-none"
              />
            </div>
            <button
              onClick={() => openModal(null)}
              className="flex items-center gap-1.5 px-3 py-1.5 rounded-lg text-xs md:text-sm bg-zinc-100 text-black hover:bg-zinc-300"
            >
              <UserPlus size={14} /> Qo'shish
            </button>
            <div className="relative min-w-[80px]">
              <select
                value={filterStatus}
                onChange={(e) => setFilterStatus(e.target.value)}
                className="w-full bg-background border border-zinc-800 rounded-lg py-1.5 pl-2 pr-7 text-xs md:text-sm outline-none cursor-pointer text-zinc-400"
              >
                <option value="">All</option>
                <option value="faol">Faol</option>
                <option value="tatilda">Tatilda</option>
                <option value="ishdan bo'shatilgan">Nofaol</option>
              </select>
              <ChevronDown
                className="absolute right-2 top-1/2 -translate-y-1/2 text-zinc-500 pointer-events-none"
                size={12}
              />
            </div>
          </div>
        </div>
        {/* Table */}
        <div className="rounded-xl border border-zinc-800 overflow-hidden w-full overflow-x-auto">
          <table className="w-full text-left min-w-[600px]">
            <thead className="text-zinc-500 text-[10px] md:text-[12px] uppercase">
              <tr>
                <th className="p-3">Ism Familiya</th>
                <th className="p-3">Email</th>
                <th className="p-3">Rol</th>
                <th className="p-3">Holat</th>
                <th className="p-3 text-right">Amallar</th>
              </tr>
            </thead>
            <tbody className="text-xs md:text-sm divide-y divide-zinc-800">
              {isLoading
                ? [...Array(5)].map((_, i) => <SkeletonRow key={i} />)
                : filteredData.map((item: AdminUser) => (
                    <tr
                      key={item._id}
                      className="hover:bg-zinc-800/30 transition-colors"
                    >
                      <td className="p-3">
                        {item.first_name} {item.last_name}
                      </td>
                      <td className="p-3 font-mono text-[10px] md:text-xs">
                        {item.email}
                      </td>
                      <td className="p-3">
                        <span className="px-2 py-0.5 rounded border border-zinc-700 text-zinc-400 text-[10px] font-bold uppercase">
                          {item.role}
                        </span>
                      </td>
                      <td className="p-3">
                        <span
                          className={`px-2 py-0.5 rounded-full text-[10px] font-bold border ${
                            item.status === "faol"
                              ? "bg-emerald-500/10 text-emerald-400 border-emerald-500/20"
                              : item.status === "tatilda"
                                ? "bg-amber-500/10 text-amber-400 border-amber-500/20"
                                : "bg-red-500/10 text-red-400 border-red-500/20"
                          }`}
                        >
                          {item.status}
                        </span>
                      </td>
                      <td className="p-3 text-right">
                        <div className="flex justify-end gap-2">
                          <button
                            onClick={() => openModal(item)}
                            className="p-1.5 hover:bg-zinc-700 rounded-md text-zinc-400"
                          >
                            <Pencil size={14} />
                          </button>
                          {item.status === "ishdan bo'shatilgan" && (
                            <button
                              onClick={() => handleStatusToggle(item)}
                              className="p-1.5 rounded-md text-emerald-500 hover:bg-emerald-900/20"
                            >
                              <RotateCcw size={14} />
                            </button>
                          )}
                          <button
                            onClick={() => handleDelete(item._id)}
                            className="p-1.5 hover:bg-red-900/40 rounded-md text-red-500"
                          >
                            <Trash2 size={14} />
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))}
            </tbody>
          </table>
        </div>
      </div>

      {/* Modal - UI qismi o'zgarmadi, faqat onSubmit handleSaveSubmit ga o'zgartirildi */}
      {isModalOpen && (
        <div className="fixed inset-0 bg-black/80 backdrop-blur-sm flex items-center justify-center z-[100] p-4">
          <div className="bg-background border border-border w-full max-w-md rounded-xl p-6 shadow-lg overflow-y-auto max-h-[90vh]">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-lg font-semibold">
                {editingAdmin ? "Tahrirlash" : "Yangi admin"}
              </h2>
              <button onClick={() => setIsModalOpen(false)}>
                <X size={18} className="text-muted-foreground" />
              </button>
            </div>
            <form onSubmit={handleSaveSubmit} className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <input
                  placeholder="Ism"
                  value={formData.first_name}
                  onChange={(e) =>
                    setFormData({ ...formData, first_name: e.target.value })
                  }
                  className="w-full h-10 rounded-md border bg-background px-3 text-sm outline-none"
                  required
                />
                <input
                  placeholder="Familiya"
                  value={formData.last_name}
                  onChange={(e) =>
                    setFormData({ ...formData, last_name: e.target.value })
                  }
                  className="w-full h-10 rounded-md border bg-background px-3 text-sm outline-none"
                  required
                />
              </div>
              <input
                placeholder="Email"
                type="email"
                value={formData.email}
                onChange={(e) =>
                  setFormData({ ...formData, email: e.target.value })
                }
                className="w-full h-10 rounded-md border bg-background px-3 text-sm outline-none"
                required
              />
              {!editingAdmin && (
                <input
                  placeholder="Parol"
                  type="password"
                  value={formData.password}
                  onChange={(e) =>
                    setFormData({ ...formData, password: e.target.value })
                  }
                  className="w-full h-10 rounded-md border bg-background px-3 text-sm outline-none"
                  required
                />
              )}
              <div className="flex gap-3 pt-4">
                <button
                  type="button"
                  onClick={() => setIsModalOpen(false)}
                  className="flex-1 border h-10 rounded-md text-sm"
                >
                  Bekor qilish
                </button>
                <button
                  type="submit"
                  disabled={saveMutation.isPending}
                  className="flex-1 bg-zinc-100 text-black h-10 rounded-md text-sm font-medium hover:bg-zinc-300"
                >
                  {saveMutation.isPending ? "Saqlanmoqda..." : "Saqlash"}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default AdminPanel;
